import assign from 'lodash.assign';
import EditorStyles from './editor-styles';
import {updateCSSWithValue} from "../helpers";

const { Fragment } = wp.element;
const { RangeControl, ToggleControl } = wp.components;
const { addFilter } = wp.hooks;
const { __ } = wp.i18n;

const enableCustomControlOnBlocks = [
	'core/paragraph',
	'core/image',
	'core/heading',
	'core/gallery',
	'core/list',
	'core/quote',
	'core/shortcode',
	'core/archives',
	'core/audio',
	'core/button',
	'core/buttons',
	'core/calendar',
	'core/categories',
	'core/code',
	'core/columns',
	'core/column',
	'core/cover',
	'core/embed',
	'core/file',
	'core/group',
	'core/freeform',
	'core/html',
	'core/media-text',
	'core/latest-comments',
	'core/latest-posts',
	'core/missing',
	'core/more',
	'core/nextpage',
	'core/preformatted',
	'core/pullquote',
	'core/rss',
	'core/search',
	'core/separator',
	'core/block',
	'core/social-links',
	'core/social-link',
	'core/spacer',
	'core/subhead',
	'core/table',
	'core/tag-cloud',
	'core/text-columns',
	'core/verse',
	'core/video'
];

// register attributes
const addCustomControlAttributes = ( settings, name ) => {
	// Do nothing if it's another block than our defined ones.
	if ( ! enableCustomControlOnBlocks.includes( name ) ) {
		return settings;
	}
	console.log(typeof settings.attributes.borderRadiusTopLeftMobile);
	// Use Lodash's assign to gracefully handle if attributes are undefined
	settings.attributes = assign( settings.attributes, {
		borderRadiusActive                    :{ type: 'boolean', default: false },
	
		borderRadiusTopLeftMobile                 :{ type: 'object',
			
			default: { value : '', unit : 'px'}
			
			},
		borderRadiusTopLeftMobileLandscape        :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusTopLeftTablet                 :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusTopLeftTabletLandscape        :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusTopLeftTabletPro              :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusTopLeftTabletProLandscape     :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusTopLeftDesktop                :{ type: 'object', default: { value : '', unit : 'px'} },
		
		borderRadiusTopRightMobile                 :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusTopRightMobileLandscape        :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusTopRightTablet                 :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusTopRightTabletLandscape        :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusTopRightTabletPro              :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusTopRightTabletProLandscape     :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusTopRightDesktop                :{ type: 'object', default: { value : '', unit : 'px'} },
		
		borderRadiusBottomLeftMobile                 :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusBottomLeftMobileLandscape        :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusBottomLeftTablet                 :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusBottomLeftTabletLandscape        :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusBottomLeftTabletPro              :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusBottomLeftTabletProLandscape     :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusBottomLeftDesktop                :{ type: 'object', default: { value : '', unit : 'px'} },
		
		borderRadiusBottomRightMobile                 :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusBottomRightMobileLandscape        :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusBottomRightTablet                 :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusBottomRightTabletLandscape        :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusBottomRightTabletPro              :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusBottomRightTabletProLandscape     :{ type: 'object', default: { value : '', unit : 'px'} },
		borderRadiusBottomRightDesktop                :{ type: 'object', default: { value : '', unit : 'px'} },
	} );
	
	return settings;
};

addFilter( 'blocks.registerBlockType', 'sv100-premium/gutenberg-extended-block-controls', addCustomControlAttributes );

// the component
function BorderRadius(props){
	const _name = 'BorderRadius';
	const _prefix = 'borderRadius';
	
	if ( ! enableCustomControlOnBlocks.includes( props.name ) ) {
		return (
			<Fragment></Fragment>
		);
	}
	console.log(props);
	const values = props.attributes;
	const currentResponsiveTab = typeof props.attributes.currentResponsiveTab !== 'undefined' ? props.attributes.currentResponsiveTab : 'Mobile';

	if(values[_prefix+'Active'] === true){
		return(
			<Fragment>
				<ToggleControl
					label={__('Border Radius', 'sv100_premium')}
					checked={values[_prefix+'Active']}
					onChange={(val) => props.setAttributes({[_prefix+'Active']: val})}
				/>
				<RangeControl
					label={__('Top-Left', 'sv100_premium')}
					value={parseInt(values[_prefix+'TopLeft'+currentResponsiveTab].value)}
					onChange={(val) => updateCSSWithValue(val, props, _name, _prefix+'TopLeft', EditorStyles) }
				/>
				<RangeControl
					label={__('Top-Right', 'sv100_premium')}
					value={values[_prefix+'TopRight'+currentResponsiveTab].value}
					onChange={(val) => updateCSSWithValue(val, props, _name, _prefix+'TopRight', EditorStyles) }
				/>
				<RangeControl
					label={__('Bottom-Left', 'sv100_premium')}
					value={values[_prefix+'BottomLeft'+currentResponsiveTab].value}
					onChange={(val) => updateCSSWithValue(val, props, _name, _prefix+'BottomLeft', EditorStyles) }
				/>
				<RangeControl
					label={__('Bottom-Right', 'sv100_premium')}
					value={values[_prefix+'BottomRight'+currentResponsiveTab].value}
					onChange={(val) => updateCSSWithValue(val, props, _name, _prefix+'BottomRight', EditorStyles) }
				/>
				
				
			</Fragment>
		);
	}else{
		return(
			<Fragment>
				<ToggleControl
					label={__('BorderRadius', 'sv100_premium')}
					checked={values[_prefix+'Active']}
					onChange={(val) => props.setAttributes({[_prefix+'Active']: val})}
				/>
			</Fragment>
		);
	}
	
	
}

export default BorderRadius;